	import wPicker from "@/components/w-picker/w-picker.vue";

	export default {
		data() {
			return {
				cross:'',
				ruleForm: {
				renwumingcheng: '',
				renwuleixing: '',
				tupian: '',
				renwushijian: '',
				renshu: '',
				huodejifen: '',
				renwudidian: '',
				renwuyaoqiu: '',
				zhanghao: '',
				qingxiemingcheng: '',
				},
				renwuleixingOptions: [],
				renwuleixingIndex: 0,
				// 登陆用户信息
				user: {},
                                ro:{
                                   renwumingcheng : false,
                                   renwuleixing : false,
                                   tupian : false,
                                   renwushijian : false,
                                   renshu : false,
                                   huodejifen : false,
                                   renwudidian : false,
                                   renwuyaoqiu : false,
                                   zhanghao : false,
                                   qingxiemingcheng : false,
                                },
			}
		},
		components: {
			wPicker
		},
		computed: {
			baseUrl() {
				return this.$base.url;
			},



		},
		async onLoad(options) {
			let table = uni.getStorageSync("nowTable");
			// 获取用户信息
			let res = await this.$api.session(table);
			this.user = res.data;
			
			// ss读取


			// 下拉框
			res = await this.$api.option(`renwuleixing`,`renwuleixing`,{});
			this.renwuleixingOptions = res.data;

			// 如果有登陆，获取登陆后保存的userid
			this.ruleForm.userid = uni.getStorageSync("userid")
			if (options.refid) {
				// 如果上一级页面传递了refid，获取改refid数据信息
				this.ruleForm.refid = options.refid;
				this.ruleForm.nickname = uni.getStorageSync("nickname");
			}
			// 如果是更新操作
			if (options.id) {
				this.ruleForm.id = options.id;
				// 获取信息
				res = await this.$api.info(`renwuxinxi`, this.ruleForm.id);
				this.ruleForm = res.data;
			}
			// 跨表
			this.cross = options.cross;
			if(options.cross){
				var obj = uni.getStorageSync('crossObj');
				for (var o in obj){
					if(o=='renwumingcheng'){
					this.ruleForm.renwumingcheng = obj[o];
					this.ro.renwumingcheng = true;
					continue;
					}
					if(o=='renwuleixing'){
					this.ruleForm.renwuleixing = obj[o];
					this.ro.renwuleixing = true;
					continue;
					}
					if(o=='tupian'){
					this.ruleForm.tupian = obj[o];
					this.ro.tupian = true;
					continue;
					}
					if(o=='renwushijian'){
					this.ruleForm.renwushijian = obj[o];
					this.ro.renwushijian = true;
					continue;
					}
					if(o=='renshu'){
					this.ruleForm.renshu = obj[o];
					this.ro.renshu = true;
					continue;
					}
					if(o=='huodejifen'){
					this.ruleForm.huodejifen = obj[o];
					this.ro.huodejifen = true;
					continue;
					}
					if(o=='renwudidian'){
					this.ruleForm.renwudidian = obj[o];
					this.ro.renwudidian = true;
					continue;
					}
					if(o=='renwuyaoqiu'){
					this.ruleForm.renwuyaoqiu = obj[o];
					this.ro.renwuyaoqiu = true;
					continue;
					}
					if(o=='zhanghao'){
					this.ruleForm.zhanghao = obj[o];
					this.ro.zhanghao = true;
					continue;
					}
					if(o=='qingxiemingcheng'){
					this.ruleForm.qingxiemingcheng = obj[o];
					this.ro.qingxiemingcheng = true;
					continue;
					}
				}
			}
			this.styleChange()
		},
		methods: {
			styleChange() {
				this.$nextTick(()=>{
					// document.querySelectorAll('.app-update-pv .cu-form-group .uni-input-input').forEach(el=>{
					//   el.style.backgroundColor = this.addUpdateForm.input.content.backgroundColor
					// })
				})
			},

			// 多级联动参数



			// 下拉变化
			renwuleixingChange(e) {
				this.renwuleixingIndex = e.target.value
				this.ruleForm.renwuleixing = this.renwuleixingOptions[this.renwuleixingIndex]
			},

			tupianTap() {
				let _this = this;
				this.$api.upload(function(res) {
					_this.ruleForm.tupian = 'upload/' + res.file;
					_this.$forceUpdate();
					_this.$nextTick(()=>{
						_this.styleChange()
					})
				});
			},

			getUUID () {
				return new Date().getTime();
			},
			async onSubmitTap() {





















//跨表计算判断
				if((!this.ruleForm.renwumingcheng)){
					this.$utils.msg(`任务名称不能为空`);
					return
				}
				if((!this.ruleForm.renwuleixing)){
					this.$utils.msg(`任务类型不能为空`);
					return
				}
				if((!this.ruleForm.renshu)){
					this.$utils.msg(`人数不能为空`);
					return
				}
				if(this.ruleForm.renshu&&(!this.$validate.isIntNumer(this.ruleForm.renshu))){
					this.$utils.msg(`人数应输入整数`);
					return
				}
				if((!this.ruleForm.huodejifen)){
					this.$utils.msg(`获得积分不能为空`);
					return
				}
				//更新跨表属性
			       var crossuserid;
			       var crossrefid;
			       var crossoptnum;
				if(this.cross){
					var statusColumnName = uni.getStorageSync('statusColumnName');
					var statusColumnValue = uni.getStorageSync('statusColumnValue');
					if(statusColumnName!='') {
						var obj = uni.getStorageSync('crossObj');
						if(!statusColumnName.startsWith("[")) {
							for (var o in obj){
								if(o==statusColumnName){
									obj[o] = statusColumnValue;
								}

							}
							var table = uni.getStorageSync('crossTable');
							await this.$api.update(`${table}`, obj);
						} else {
						       crossuserid=Number(uni.getStorageSync('userid'));
						       crossrefid=obj['id'];
						       crossoptnum=uni.getStorageSync('statusColumnName');
						       crossoptnum=crossoptnum.replace(/\[/,"").replace(/\]/,"");
						}
					}
				}
				if(crossrefid && crossuserid) {
					this.ruleForm.crossuserid=crossuserid;
					this.ruleForm.crossrefid=crossrefid;
					let params = {
						page: 1,
						limit:10,
						crossuserid:crossuserid,
						crossrefid:crossrefid,
					}
					let res = await this.$api.list(`renwuxinxi`, params);
					if (res.data.total >= crossoptnum) {
						this.$utils.msg(uni.getStorageSync('tips'));
						return false;
					} else {
                //跨表计算
						if(this.ruleForm.id){
							await this.$api.update(`renwuxinxi`, this.ruleForm);
						}else{
							await this.$api.add(`renwuxinxi`, this.ruleForm);
						}
						this.$utils.msgBack('提交成功');
					}
				} else {
                //跨表计算
					if(this.ruleForm.id){
						await this.$api.update(`renwuxinxi`, this.ruleForm);
					}else{
						await this.$api.add(`renwuxinxi`, this.ruleForm);
					}
					this.$utils.msgBack('提交成功');
				}
			},
			optionsChange(e) {
				this.index = e.target.value
			},
			bindDateChange(e) {
				this.date = e.target.value
			},
			getDate(type) {
				const date = new Date();
				let year = date.getFullYear();
				let month = date.getMonth() + 1;
				let day = date.getDate();
				if (type === 'start') {
					year = year - 60;
				} else if (type === 'end') {
					year = year + 2;
				}
				month = month > 9 ? month : '0' + month;;
				day = day > 9 ? day : '0' + day;
				return `${year}-${month}-${day}`;
			},
			toggleTab(str) {
				this.$refs[str].show();
			}
		}
	}
